
import { defineComponent, onMounted, ref } from "vue";
import { useStore } from "vuex";
import { useRouter, useRoute } from "vue-router";
import IConcessionary from "@/store/modules/concessionary/interface/concessionary.interface"
import ISite from "@/store/modules/site/interface/site.interface"
import ConcessionaryService from "@/store/modules/concessionary/service/concessionary.service";
import SiteService from "@/store/modules/site/service/site.service";
import MessageService from "@/store/modules/message/service/message.service";
import IMessage from "@/store/modules/message/interface/message.interface";
import Swal from "sweetalert2/dist/sweetalert2.js";
import Multiselect from '@vueform/multiselect';

export default defineComponent({
	name: "MessagesCreate",
	components: {
		Multiselect
	},
	setup() {
		const store = useStore();
    	const router = useRouter();
    	const route = useRoute();

		const concessionaryService = new ConcessionaryService();
		const siteService = new SiteService();
		const messageService = new MessageService();

		const concessionaries = ref<Array<IConcessionary>>([]);
		const sites = ref<Array<ISite>>([]);

		const message = ref<IMessage>({
			id: null,
			message: '',
			concessionary_id: null,
			site_id: null,
			visible: false
		});

		const getConcessionaries = (sedeId) => concessionaryService.getDataBySede(sedeId)
		.then((response) => {
			concessionaries.value = [...[{id: null, name: 'Todos los concesionarios'}], ...response.data]
		})
		.catch((error) => {
			
		});
		
		const getSites = () => siteService.getData()
		.then((response) => {
			sites.value = [...[{id: null, name: 'Todas las sedes'}], ...response.data]
			getData()
		})
		.catch((error) => {
			
		});

		const changeSite = (e) => {
			if(e && e != ''){
				getConcessionaries(getSiteName(e))
			}
			else{
				concessionaries.value = []
				message.value.concessionary_id = null
				message.value.site_id = null
			}
		}

		const getSiteName = (id) => {
			let siteName = ''
			sites.value.forEach(site =>{
				if(site.id == id){
					siteName = site.name
				}
			})
			return siteName
		}

		const getData = () => messageService.edit(route.params.id)
		.then((response) => {
			response.data.visible = (response.data.visible == 1)
			message.value = response.data
			if(message.value.site_id){
				getConcessionaries(getSiteName(message.value.site_id))
			}
		})
		.catch((error) => {
			Swal.fire(
			'Oh no!',
			'Informacin no encontrada',
			'error'
			);
			router.push({ name: "blocks-messages" });
		});

		const validateForm = () => {
			let disabled = false;
			if(message.value.message.length == 0) disabled = true;
			return disabled
		}

		const saveData = () => {
			Swal.fire({
				title: 'Confirmacin!',
				text: "Esta seguro que desea actualizar los datos?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Actualizar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						messageService.update(message.value.id, message.value)
						.then((response) => {
							Swal.fire(
							'Confirmacin!',
							'Informacin actualizada exitosamente',
							'success'
							).then(result => {
								router.push({ name: "blocks-messages" });
							});
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se actualizaron los datos',
							'error'
							);
						});
					}
			})
			
		}

		

		onMounted(() => {
			getSites()
			getData()
		});

		return {
            concessionaries,
			sites,
			getConcessionaries,
			getSites,
			validateForm,
			saveData,
			getData,
			message,
			changeSite
        };
	},
});
